body {
    background: #1C1E2B !important;
    height: 3000px;
    width: 100vw;
    overflow: hidden;
    overflow-y: auto;
}

@media (max-width: 600px) {
    .header img {
        margin: 0 10px !important;
    }

    #codeoverlay {
        background: rgba(0, 0, 0, 0.5) !important;
    }

    .subtitle {
        color: white !important;
    }

    .section2 {
        background: transparent linear-gradient(180deg, #040507 0%, #000000 11%, #000000 100%) !important;
    }
}

#codeoverlay {
    top: -46px;
    left: 0;
    width: 100%;
    height: 105vh;
    position: absolute;
}

html {
    scroll-behavior: smooth;
    overflow: hidden;
    overflow-y: auto;
}

cos {
    font-size: 5em;
    color: lightgreen;
    font-weight: bold;

}

.vis {
    opacity: 1 !important;
    background: url("../codeimage.png") center / contain no-repeat;
}

#code {
    opacity: 0;
    top: -46px;
    left: 2w;
    width: 470px;
    height: 105vh;
    transition: opacity 1s ease-out;
    // background: transparent url('../codeimage.png') 0% 0% no-repeat padding-box;
    // background-size: contain;
    // background-repeat: no-repeat;
    position: absolute;
    max-width: 99vw;
}

.header {
    width: 100vw;
    height: 0px;
    background: rgba(255, 255, 255, 0);
    position: fixed;
    // box-shadow: 0px 10px 14px rgba(0, 0, 0, 0);


    img {
        position: relative;
        cursor: pointer;
        top: 4px;
        transition: top 0.4s;
        z-index: 9999;
    }

    img:hover {
        top: 54px;
    }
}

.wavecont {
    width: 100vw;
    height: 117px;
    overflow: hidden;
    position: absolute;
    top: 83vh;
    left: 0;
}

#wave1 {
    // top: 83vh;
    left: -38.666648864746094px;
    width: 3035px;
    height: 117px;
    position: relative;
    background: transparent url('../wave.png');
    background-size: 2277px 117px;
    background-repeat: no-repeat;
    z-index: -1;
}

#wave2 {
    // top: 83vh;
    left: -357px;
    width: 2277px;
    height: 117px;
    position: relative;
    transform: translate(0, -100%);
    background: transparent url('../wave.png');
    background-size: 2277px 117px;
    background-repeat: no-repeat;
    z-index: -1;

}

.section2 {
    height: 2000px;
    position: absolute;
    width: 100%;
    overflow: hidden;
    top: 99vh;
    background: transparent linear-gradient(180deg, #090a0e 0%, #000000 11%, #000000 100%);

    .card {
        // height: 300px;
    }

    .firstset {
        margin-top: 200px;
        width: 100%;
        max-width: 100vw;
        margin-left: 0;
        margin-right: 0;
    }
}

.card {
    border: none !important;
}


.section1 {
   top: 180px;
   position: relative;
}

.front {
    z-index: 99999;
}

.title {
    width: 300px;
    display: block;
}

@keyframes subtitle {
    to {
        transform: translateY(0px);
        opacity: 1;
    }
}

.subtitle {
    font-family: 'Roboto';
    font-size: 20px;
    text-align: center;
    color: #8186D5;
    transform: translateY(-50px);
    opacity: 0;
    animation: subtitle 1.4s 1s forwards;
}

.seework {
    background: transparent linear-gradient(112deg, #7195F5 0%, #C397F4 100%) 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 5px #000000B8;
    border: 1px solid #707070;
    border-radius: 13px;
    height: 42px;
    cursor: pointer;
    display: flex;

    &:hover i {
        transform: translate(33px, 0px);
    }

    &:hover i span {
        width: 57px;
        right: 24px;
    }

    i {
        margin: auto;
        text-align: center;
        align-items: center;
        align-self: center;
        transition: .4s;
        color: #FEB57E;

        span {
            overflow: hidden;
            width: 0.1px;
            transition: .4s;
            white-space: nowrap;
            font-size: 15px;
            right: 16px;
            align-items: center;
            align-self: center;
            position: absolute;
            font-family: 'Roboto';
            font-weight: 300;
            color: #FEB57E;
            user-select: none;
        }
    }
}

